import { Builder } from '../../Builder'

export interface InputContactMessageContentPayload {
    phone_number: string
    first_name: string
    last_name?: string
    vcard?: string
}

export class InputContactMessageContent extends Builder<InputContactMessageContentPayload> {

    public constructor() {
        super({ phone_number: '', first_name: '' })
    }

    public phoneNumber(value: string): this {
        this.packet.phone_number = value
        return this
    }

    public firstName(value: string): this {
        this.packet.first_name = value
        return this
    }

    public lastName(value?: string): this {
        this.packet.last_name = value
        return this
    }

    public vCard(value?: string): this {
        this.packet.vcard = value
        return this
    }

}
